.container {
    padding: var(--spacing-6);
    max-width: 1400px;
}

/* Quick Stats */
.quickStats {
    display: flex;
    gap: var(--spacing-4);
    margin-bottom: var(--spacing-6);
}

@media (max-width: 768px) {
    .quickStats {
        flex-direction: column;
    }
}

.quickStat {
    display: flex;
    align-items: center;
    gap: var(--spacing-4);
    padding: var(--spacing-4) var(--spacing-5);
    background: var(--color-primary-bg-light);
    border-radius: var(--radius-xl);
    border: 1px solid rgba(255, 255, 255, 0.05);
}

.quickStatIcon {
    width: 48px;
    height: 48px;
    border-radius: var(--radius-lg);
    background: rgba(184, 224, 63, 0.15);
    color: var(--color-accent);
    display: flex;
    align-items: center;
    justify-content: center;
}

.quickStatIcon.pink {
    background: rgba(232, 139, 195, 0.15);
    color: var(--color-pink);
}

.quickStatIcon.success {
    background: rgba(74, 222, 128, 0.15);
    color: var(--color-success);
}

.quickStatValue {
    display: block;
    font-size: var(--font-size-xl);
    font-weight: 700;
    color: var(--color-white);
}

.quickStatLabel {
    display: block;
    font-size: var(--font-size-sm);
    color: var(--color-gray-400);
}

/* Toolbar */
.toolbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: var(--spacing-4);
    margin-bottom: var(--spacing-5);
}

.searchWrapper {
    width: 320px;
}

@media (max-width: 640px) {
    .toolbar {
        flex-direction: column;
        align-items: stretch;
    }

    .searchWrapper {
        width: 100%;
    }
}

/* Consumer Cell */
.consumerCell {
    display: flex;
    align-items: center;
    gap: var(--spacing-3);
}

.consumerAvatar {
    width: 40px;
    height: 40px;
    border-radius: var(--radius-full);
    background: linear-gradient(135deg, var(--color-accent) 0%, var(--color-leaf-dark) 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    font-size: var(--font-size-xs);
    color: var(--color-primary-bg);
    flex-shrink: 0;
}

.consumerInfo {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.consumerName {
    font-weight: 500;
    color: var(--color-white);
}

.consumerEmail {
    font-size: var(--font-size-xs);
    color: var(--color-gray-500);
}

/* Pickups Cell */
.pickupsCell {
    display: flex;
    align-items: baseline;
    gap: var(--spacing-1);
}

.pickupsCount {
    font-weight: 600;
    color: var(--color-accent);
}

.rescueLabel {
    font-size: var(--font-size-xs);
    color: var(--color-gray-500);
}

/* Saved Amount */
.savedAmount {
    font-weight: 600;
    color: var(--color-success);
}

/* Rating */
.rating {
    display: flex;
    align-items: center;
    gap: var(--spacing-1);
}

.ratingIcon {
    color: var(--color-warning);
}

.noRating {
    color: var(--color-gray-500);
}

/* Date */
.dateText {
    color: var(--color-gray-400);
    font-size: var(--font-size-sm);
}

/* Actions */
.actions {
    display: flex;
    gap: var(--spacing-2);
}

.actionBtn {
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: var(--radius-md);
    color: var(--color-gray-400);
    transition: all var(--transition-fast);
}

.actionBtn:hover {
    background: rgba(255, 255, 255, 0.1);
    color: var(--color-white);
}